@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&family=Roboto:wght@900&display=swap);
*{
  margin:0;
  padding:0;
  font-family: 'Noto Serif', serif;
  box-sizing:border-box;
}
body{
  min-height:100vh;
  display:flex;
  justify-content:center;
  align-items:center;
  background-color:#3498db;
}
/* Header Style */
header{
  width:600px;
  padding:1rem 0;
  color:#fff;
  font-size:1.2rem;
  background-color:#222;
  text-align:center;
  margin-bottom:1.2rem;
}
header .link{
  color:#fff;
  font-size:1rem;
  text-decoration:none;
  outline:none;
}
/* Todo List Style */
.todo-list{
  min-width:400px;
  min-height:280px;
  background-color:#ddd;
  border-radius:20px;
  padding:1rem;
}
/* Form Style */
form{
  margin-bottom:10px;
}
input[type="text"]{
  padding:1rem .8rem;
  color:#fff;
  font-size:1.2rem;
  background-color:rgb(9, 165, 48);
  border:none;
  outline:none;
  border-radius:10px;
  cursor: default;
}
input[type="text"]::-webkit-input-placeholder{
  color:rgba(255, 255, 255, .5);
}
input[type="text"]:-ms-input-placeholder{
  color:rgba(255, 255, 255, .5);
}
input[type="text"]::-ms-input-placeholder{
  color:rgba(255, 255, 255, .5);
}
input[type="text"]::placeholder{
  color:rgba(255, 255, 255, .5);
}
input[type="submit"]{
  padding:.8rem 2rem;
  color:#fff;
  font-size:1rem;;
  background-color:rgb(228, 165, 48);
  border-radius:10px;
  outline:none;
  border:none;
  cursor:pointer;
  margin-left:10px;
}
/* Todo Item Style */
.todo-item{
  min-width:100%;
  padding:1rem .8rem;
  color:#222;
  font-size:1.2rem;
  background-color:#fff;/*rgb(209, 45, 195)*/
  border-radius:10px;
  display:flex;
  justify-content:space-between;
  align-items:center;
  cursor: default;
  box-shadow:0 0 4px 0px rgba(0,0,0,.4);
}
.todo-item:not(:last-child){
  margin-bottom:10px;
}
.todo-item .btn{
  color:#fff;
  font-size:1.4rem;
  cursor:pointer;
}
.todo-item .btn:first-child{
  margin-left:20px;
}
.todo-item .btn:last-child{
  margin-left:10px;
}
/* About Section Style */
.about{
  width:600px;
  color:#fff;
  font-size:1.2rem;
  font-style:normal;
  line-height:2;
  font-family:Tahoma;
  text-align:center;
}
.about h1{
  color:#222;
  font-size:1.8rem;
}
/* Media Quiries */
@media screen and (max-width:900px){
  header, .todo-list, .about{
    width:400px;
  }
  .todo-item{
    text-overflow: unset;
    font-size:1rem;
  }
  input{
    font-size:1rem;
  }
  input[type="text"]{
    max-width:200px;
    }
}
